export const initializeSampleData = () => {
  try {
    if (typeof window === 'undefined') return;
    
    // Check if data already exists
    const existingWorkers = localStorage.getItem('labour_workers');
    const existingAttendance = localStorage.getItem('labour_attendance');
    
    // Only initialize if no data exists
    if (!existingWorkers) {
      localStorage.setItem('labour_workers', JSON.stringify(sampleWorkers));
    }
    
    if (!existingAttendance) {
      // Create some sample attendance records for today
      const today = formatDate(new Date());
      const sampleAttendance: Attendance[] = [
        {
          id: generateId(),
          workerId: sampleWorkers[0].id,
          date: today,
          checkIn: '08:00',
          checkOut: null,
          totalHours: 0,
          status: 'present'
        },
        {
          id: generateId(),
          workerId: sampleWorkers[1].id,
          date: today,
          checkIn: '07:30',
          checkOut: null,
          totalHours: 0,
          status: 'present'
        }
      ];
      localStorage.setItem('labour_attendance', JSON.stringify(sampleAttendance));
    }
  } catch (error) {
    console.error('Error initializing sample data:', error);
  }
};
