'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Worker, Attendance, AttendanceStats } from '@/lib/types';
import { getWorkers, getAttendanceForDate, formatDate } from '@/lib/storage';
import { initializeSampleData } from '@/data/mockData';

export default function Dashboard() {
  const [stats, setStats] = useState<AttendanceStats>({
    totalWorkers: 0,
    presentToday: 0,
    absentToday: 0,
    totalHoursToday: 0
  });
  const [recentAttendance, setRecentAttendance] = useState<(Attendance & { workerName: string })[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Initialize sample data on first load
    initializeSampleData();
    loadDashboardData();
  }, []);

  const loadDashboardData = () => {
    try {
      const workers = getWorkers();
      const today = formatDate(new Date());
      const todayAttendance = getAttendanceForDate(today);

      // Calculate stats
      const activeWorkers = workers.filter(w => w.isActive);
      const presentWorkers = todayAttendance.filter(a => a.status === 'present');
      const totalHours = todayAttendance.reduce((sum, a) => sum + a.totalHours, 0);

      setStats({
        totalWorkers: activeWorkers.length,
        presentToday: presentWorkers.length,
        absentToday: activeWorkers.length - presentWorkers.length,
        totalHoursToday: totalHours
      });

      // Get recent attendance with worker names
      const recentWithNames = todayAttendance
        .map(attendance => {
          const worker = workers.find(w => w.id === attendance.workerId);
          return {
            ...attendance,
            workerName: worker?.name || 'Unknown Worker'
          };
        })
        .sort((a, b) => {
          if (a.checkIn && b.checkIn) {
            return b.checkIn.localeCompare(a.checkIn);
          }
          return 0;
        })
        .slice(0, 5);

      setRecentAttendance(recentWithNames);
    } catch (error) {
      console.error('Error loading dashboard data:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg text-gray-600">Loading dashboard...</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-600 mt-1">Overview of today's attendance and worker statistics</p>
        </div>
        <div className="flex space-x-3">
          <Button asChild>
            <a href="/workers/new">Add Worker</a>
          </Button>
          <Button variant="outline" asChild>
            <a href="/attendance">Mark Attendance</a>
          </Button>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Workers</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalWorkers}</div>
            <p className="text-xs text-gray-600">Active workers</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Present Today</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{stats.presentToday}</div>
            <p className="text-xs text-gray-600">Workers checked in</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Absent Today</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{stats.absentToday}</div>
            <p className="text-xs text-gray-600">Workers not present</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Hours</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalHoursToday.toFixed(1)}</div>
            <p className="text-xs text-gray-600">Hours worked today</p>
          </CardContent>
        </Card>
      </div>

      {/* Recent Activity */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        <Card>
          <CardHeader>
            <CardTitle>Recent Check-ins</CardTitle>
          </CardHeader>
          <CardContent>
            {recentAttendance.length > 0 ? (
              <div className="space-y-3">
                {recentAttendance.map((attendance) => (
                  <div key={attendance.id} className="flex justify-between items-center py-2 border-b border-gray-100 last:border-b-0">
                    <div>
                      <p className="font-medium text-gray-900">{attendance.workerName}</p>
                      <p className="text-sm text-gray-600">
                        Check-in: {attendance.checkIn || 'Not checked in'}
                      </p>
                    </div>
                    <div className="text-right">
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                        attendance.status === 'present' 
                          ? 'bg-green-100 text-green-800' 
                          : 'bg-red-100 text-red-800'
                      }`}>
                        {attendance.status}
                      </span>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <p className="text-gray-600 text-center py-4">No attendance records for today</p>
            )}
          </CardContent>
        </Card>

        <Card>
          <CardHeader>
            <CardTitle>Quick Actions</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="space-y-3">
              <Button asChild className="w-full justify-start">
                <a href="/attendance">Mark Daily Attendance</a>
              </Button>
              <Button variant="outline" asChild className="w-full justify-start">
                <a href="/workers">Manage Workers</a>
              </Button>
              <Button variant="outline" asChild className="w-full justify-start">
                <a href="/attendance/reports">View Reports</a>
              </Button>
              <Button variant="outline" asChild className="w-full justify-start">
                <a href="/attendance/history">Attendance History</a>
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
